export default {
  computed: {
    subjectName: function subjectName() {
      return this.$store.getters["examStore/subjectName"];
    },
    levelName: function levelName() {
      return this.$store.getters["examStore/levelName"];
    },
    year: function year() {
      return this.$store.getters["examStore/year"];
    },
    score: function score() {
      return this.$store.getters["examStore/score"];
    },
    totalNumber: function totalNumber() {
      return this.$store.getters["examStore/totalNumber"];
    }
  },
  methods: {
    calculatePercentage: function calculatePercentage() {
      var context = this;
      var dp = 0;
      var score = this.$store.getters["examStore/score"];
      var totalNumber = this.$store.getters["examStore/totalNumber"];
      var percentage = (score / totalNumber * 100).toFixed(dp);
      return percentage;
    }
  },
  created: function created() {
    this.$store.commit("authenticationStore/setIsError", false);
    this.$store.commit("authenticationStore/setErrorMessages", "");
  }
};